body {
  font-weight: 700;
  font-style: normal;
  font-size: 14px;
}

.img {
  transition: all 0.3s ease-out 0s;
}

.f-left {
  float: left;
}

.f-right {
  float: right;
}

.fix {
  overflow: hidden;
}

a,
.button {
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
}

a:focus,
.button:focus {
  text-decoration: none;
  outline: none;
}

a,
button {
  color: #1696e7;
  outline: medium none;
}

button:focus, input:focus, input:focus, textarea, textarea:focus {
  outline: 0;
}

.uppercase {
  text-transform: uppercase;
}

.capitalize {
  text-transform: capitalize;
}

a:hover {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 900;
  color: #373737;
  margin: 0px;
  font-style: normal;
  font-family: "Nunito", sans-serif;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: inherit;
}

h1 {
  font-size: 48px;
  font-weight: 900;
}

h2 {
  font-size: 48px;
}

h3 {
  font-size: 40px;
}

h4 {
  font-size: 32px;
}

h5 {
  font-size: 24px;
}

h6 {
  font-size: 20px;
}

ul {
  margin: 0px;
  padding: 0px;
}

li {
  list-style: none;
}

p {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  color: #000;
  margin-bottom: 0px;
}

hr {
  border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  border-top: 0 none;
  margin: 18px 0;
}

label {
  cursor: pointer;
  font-size: 18px;
  font-weight: 300;
}

*::-moz-selection {
  background: #d6b161;
  color: #fff;
  text-shadow: none;
}

::-moz-selection {
  background: #444;
  color: #fff;
  text-shadow: none;
}

::selection {
  background: #444;
  color: #fff;
  text-shadow: none;
}

*::-moz-placeholder {
  color: #555555;
  font-size: 14px;
  opacity: 1;
}

*::placeholder {
  color: #555555;
  font-size: 14px;
  opacity: 1;
}

.theme-overlay {
  position: relative;
}

.theme-overlay::before {
  background: #EA5B0C;
  content: "";
  height: 100%;
  left: 0;
  opacity: 0.6;
  position: absolute;
  top: 0;
  width: 100%;
}

.separator {
  border-top: 1px solid #f2f2f2;
}

/* button style */
.btn {
  -moz-user-select: none;
  background: #fff;
  color: #000;
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
  border-radius: 0px;
  letter-spacing: 1px;
  line-height: 1;
  margin-bottom: 0;
  padding: 15px 20px;
  text-align: center;
  text-transform: matrix(-1, 0, 0, 1, 0, 0);
  touch-action: manipulation;
  transition: all 0.3s ease 0s;
  vertical-align: middle;
  white-space: nowrap;
  font-size: 16px;
  border-radius: 30px;
  border: none;
  border-color: #000;
  text-decoration: inherit;
  font-family: system-ui;
  border-image-slice: 1;
}

.btn:hover {
  color: #000;
}

.breadcrumb > .active {
  color: #888;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
  display: inline-block;
}

a {
  text-decoration: none;
  font-weight: 500;
  font-family: "Jost";
  font-size: 14px;
  color: #545151;
}
a:hover {
  color: #000;
}

img {
  vertical-align: middle;
  border-style: none;
  max-width: 100%;
}

.section-title {
  margin-top: 60px;
}
.section-title h2 {
  font-size: 48px;
  color: #fff;
  font-weight: 900;
  margin-top: 30px;
}
@media (min-width: 250px) {
  .section-title h2 {
    font-size: 40px;
  }
}
.section-title h5 {
  margin-top: 5px;
  color: #fff;
  font-size: 16px;
  margin-bottom: 10px;
}
@media (min-width: 250px) {
  .section-title h5 {
    font-size: 13px;
  }
}

.mt-80 {
  margin-top: 70px;
}

.inner-wrapper {
  margin: 0 40px;
}

@media (min-width: 768px) {
  h4 {
    font-size: 13px;
  }
}

