.footer-wrapper{
    background-color: $current-Olive;
    padding: 60px 0;

    .footer-logo{
        margin: 50px 0 24px 0;
        text-align: center;
        img{
            display: inline-block;
        }
    }
    .footer-socail{
        text-align:center;
        ul{
            li{
                margin: 0 19px;
            }
        }
    }
}

.footer-right-menu{
    margin: 50px 0 42px 0;
    .sinel-menu-item{
        
        h6{
            font-family: 'GT Super Txt Trial';
font-style: normal;
font-weight: 700;
font-size: 20px;
line-height: 30px;
color: #F8F8F8;
margin-bottom: 40px;
        }
    }
    .singel-menu-item{
        margin-top: 40px 24px;
        li{
            display: block;
            a{
                font-family: 'Jost';
                font-style: normal;
                font-weight: 400;
                font-size: 20px;
                display: block;
                line-height: 30px;
                color: #F8F8F8;
                margin: 16.5px 0;

        }}
    }
}


.footer-copy-right{
background-color: $current-Olive;
padding: 30px;
border-top: $border-top;
border-bottom: $border-bottom;
border-color: $current-Mild-Olive;
.f-copy-text{
    p{
        font-family: 'Jost';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 1;
line-height: 24px;
color: #F8F8F8;
    }
}}


.footer-wrapper{
    position: relative;
    .footer-human-shap{
        position: absolute;
        left: 0;
        top: -154px;
        left: 363px;
    }
}